.top-bar {
  padding-block:  24px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-block: 36px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo--link {
    display: flex;
  }

  &__logo {
    height: 38px;

    @include on-tablet {
      height: 56px;
    }
  }
}



